.form-field {
  margin: 30px 0;
}

label {
  display: block;
  margin: 7px 15px;
}

.inputs {
  padding: 10px;
  margin: 0 12px;
  font-size: 16px;
  border: 1px solid #ccc;
  border-radius: 4px;
  width: 300px;
}

.inputs:focus {
  outline: none;
  border: 1.5px solid #1a73e8;
}
input[type="radio"],
input[type="checkbox"] {
  margin: 15px;
}
